/*
* Style Works for Editor Only
*/
.next-block-accordion .accordion-item ~ .block-list-appender.wp-block {
    bottom: -30px;
    left: 50%;
}

// Color controls.
.next-accordion-block-color-controls {
    display: flex;
	justify-content: space-between;
    align-items: center;
	margin-bottom: 10px;
}
.next-accordion-block-color-controls p {
	margin-bottom: 0;
}
.next-accordion-block-color-controls button.components-button {
	padding: 0;
}
.next-accordion-block-color-controls .components-button:focus:not(:disabled) {
	box-shadow: none;
	outline: none;
}
.next-accordion-block-color-controls button.components-button span.component-color-indicator {
	width: 30px;
	height: 30px;
}

.next-block-accordion .accordion .accordion-header .accordion-button {
	font-weight: 600;
}
